.button {
  padding: 0.7rem 1.2rem;
  margin: 0.5rem;
  background-color: #fcfbd8;
  color: black;
  border-radius: 50px;
  text-align: center; }

.button:hover {
  background-color: black;
  color: whitesmoke; }

@-webkit-keyframes slideInLeft {
  0% {
    -webkit-transform: translate(0, -0.5rem);
    transform: translate(0, -0.5rem);
    opacity: 0; }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    opacity: 1; } }

@keyframes slideInLeft {
  0% {
    -webkit-transform: translate(0, -0.5rem);
    transform: translate(0, -0.5rem);
    opacity: 0; }
  100% {
    -webkit-transform: translate(0, 0);
    transform: translate(0, 0);
    opacity: 1; } }

#nav-checkbox,
#toggle {
  display: none; }

.site-header {
  background-color: #fcfbd8;
  padding: 2rem; }
  .site-header .navigation {
    padding-top: 2rem; }
    .site-header .navigation ul {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      -webkit-box-orient: horizontal;
      -webkit-box-direction: normal;
      -ms-flex-direction: row;
      flex-direction: row;
      -webkit-box-align: center;
      -ms-flex-align: center;
      align-items: center;
      -webkit-box-pack: space-evenly;
      -ms-flex-pack: space-evenly;
      justify-content: space-evenly; }
      .site-header .navigation ul li {
        border: 5px solid transparent;
        height: 2rem;
        padding-left: 1rem;
        -webkit-animation: slideInLeft 0.5s 1 ease forwards;
        animation: slideInLeft 0.5s 1 ease forwards; }
      .site-header .navigation ul li:hover > a,
      .site-header .navigation ul li:active > a {
        color: #a30606; }
      .site-header .navigation ul li:hover > ul,
      .site-header .navigation ul li:active > ul {
        -webkit-transform: translateY(0);
        transform: translateY(0);
        -webkit-transform: translateX(-3rem);
        transform: translateX(-3rem);
        -webkit-animation: slideInLeft 0.5s 1 ease forwards;
        animation: slideInLeft 0.5s 1 ease forwards; }
      .site-header .navigation ul li > ul {
        display: block;
        opacity: 0;
        position: absolute;
        width: 10rem;
        -webkit-transform: translateY(2rem);
        transform: translateY(2rem);
        text-align: left;
        background-color: #fcfbd8;
        border-radius: 10%;
        border-top: 2rem solid transparent;
        margin-left: -1.4rem; }

@media screen and (max-width: 640px) {
  #toggle {
    display: block;
    text-align: center;
    padding: 1rem 0 1rem 1rem; }
  nav {
    display: none; }
  #nav-checkbox:checked + header nav {
    display: block;
    position: relative;
    padding: 0;
    text-align: center; }
  #nav-checkbox:checked + header nav > ul {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-flow: column;
    flex-flow: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    padding-bottom: 0; }
  #nav-checkbox:checked + header nav ul li ul {
    margin-top: -6.5rem;
